import newsNavBar from "../../components/news/news-nav-bar.vue";
import commonList from "../../components/common/common-list.vue";
import loadMore from "../../components/common/load-more.vue";
import topicNav from "../../components/news/topic-nav.vue";
import topicList from "../../components/news/topic-list.vue";
export default {
	components:{
		newsNavBar,
		commonList,
		loadMore,
		topicNav,
		topicList
	},
	data() {
		return {
			swiperheight:500,
			tabIndex:0,
			tabBars:[
				{name:"关注",id:"guanzhu"},
				{name:"话题",id:"topic"}
			],
			guanzhu:{
				loadtext:"上拉加载更多",
				list:[
					// 文字
					{
						userpic:"../../static/demo/userpic/12.jpg",
						username:"哈哈",
						sex:0, //0 男 1 女
						age:25,
						isguanzhu:false,
						title:"我是标题",
						titlepic:"",
						video:false,
						share:false,
						path:"深圳 龙岗",
						sharenum:20,
						commentnum:30,
						goodnum:20
					},
					// 图文
					{
						userpic:"../../static/demo/userpic/12.jpg",
						username:"哈哈",
						sex:0, //0 男 1 女
						age:25,
						isguanzhu:false,
						title:"我是标题",
						titlepic:"../../static/demo/datapic/13.jpg",
						video:false,
						share:false,
						path:"深圳 龙岗",
						sharenum:20,
						commentnum:30,
						goodnum:20
					},
					// 视频
					{
						userpic:"../../static/demo/userpic/12.jpg",
						username:"哈哈",
						sex:0, //0 男 1 女
						age:25,
						isguanzhu:false,
						title:"我是标题",
						titlepic:"../../static/demo/datapic/13.jpg",
						video:{
							looknum:"20w",
							long:"2:47"
						},
						share:false,
						path:"深圳 龙岗",
						sharenum:20,
						commentnum:30,
						goodnum:20
					},
					// 分享
					{
						userpic:"../../static/demo/userpic/12.jpg",
						username:"哈哈",
						sex:0, //0 男 1 女
						age:25,
						isguanzhu:false,
						title:"我是标题",
						titlepic:"",
						video:false,
						share:{
							title:"我是分享的标题",
							titlepic:"../../static/demo/datapic/14.jpg"
						},
						path:"深圳 龙岗",
						sharenum:20,
						commentnum:30,
						goodnum:20
					},
				]
			},
			topic:{
				swiper:[],
				nav:[],
				list:[]
			}
		};
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res)=> {
				let height=res.windowHeight-uni.upx2px(100)
				this.swiperheight=height;
			}
		});
		this.__init();
	},
	methods:{
		// 初始化数据
		__init(){
			this.getSwiper();
			this.getNav();
			this.getHot();
		},
		// 打开搜索页
		openSearch(){
			uni.navigateTo({
				url: '../search/search?searchType=topic'
			});
		},
		// 获取广告
		async getSwiper(){
			let [err,res] = await this.$http.get('/adsense/0');
			if (!this.$http.errorCheck(err,res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					src:list[i].src,
					url:list[i].url
				})
			}
			this.topic.swiper = arr;
		},
		// 获取热门分类
		async getNav(){
			let [err,res] = await this.$http.get('/topicclass');
			if (!this.$http.errorCheck(err,res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id:list[i].id,
					name:list[i].classname
				})
			}
			this.topic.nav = arr;
		},
		// 获取热门话题
		async getHot(){
			let [err,res] = await this.$http.get('/hottopic');
			if (!this.$http.errorCheck(err,res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					id:list[i].id,
					titlepic:list[i].titlepic,
					title:list[i].title,
					desc:list[i].desc,
					totalnum:list[i].post_count,
					todaynum:list[i].todaypost_count,
				})
			}
			this.topic.list = arr;
		},
		// 点击切换
		changeTab(index){
			this.tabIndex=index;
		},
		// 滑动事件
		tabChange(e){
			this.tabIndex=e.detail.current;
		},
		// 上拉加载
		loadmore(){
			if(this.guanzhu.loadtext!="上拉加载更多"){ return; }
			// 修改状态
			this.guanzhu.loadtext="加载中...";
			// 获取数据
			setTimeout(()=> {
				//获取完成
				let obj={
					userpic:"../../static/demo/userpic/12.jpg",
					username:"哈哈",
					sex:0, //0 男 1 女
					age:25,
					isguanzhu:false,
					title:"我是标题",
					titlepic:"../../static/demo/datapic/13.jpg",
					video:false,
					share:false,
					path:"深圳 龙岗",
					sharenum:20,
					commentnum:30,
					goodnum:20
				};
				this.guanzhu.list.push(obj);
				this.guanzhu.loadtext="上拉加载更多";
			}, 1000);
			// this.guanzhu.loadtext="没有更多数据了";
		}
	}
}
